<template>
<div>
  <e-header :uiType='uiType' :pageType='pageType' @typeChange='handleUiTypeChange'></e-header>
  <div :class="[$style.bg, 'u-clearfix']">
    <video-player :class="[$style['v-content']]" :options="vOptions" v-on:changeVideoWidth="changeVideoWidth" :videoInfos="videoInfos" :hlsList="hlsPlayList" :videoExists="videoExists" v-on:goToVideo="goToVideo"></video-player>
  </div>
  <div :class="$style.videoInfo">
    <h1 :class="$style.videoTitle">{{videoInfos.name}}</h1>
    <div :class="$style.videoActor">
      <label>?????????</label>
      <p>{{videoInfos.actor ? videoInfos.actor : ''}}<span style="display:inline-block;"></span></p>
    </div>
    <div :class="$style.videoSummary">
      <label>?????????</label>
      <p>
        <span :class="$style.videoSummaryText" v-if="videoSummaryShow">{{videoInfos.intro}}</span>
        <span :class="$style.videoSummaryText" v-else>{{videoInfos.intro.slice(0, 150)}}<em v-show="videoInfos.intro.length > 150">...</em></span>
        <span :class="$style.videoSummaryMore" @click="changeSummary(videoSummaryShow)" v-show="videoInfos.intro.length > 150">{{videoSummaryName}}</span>
      </p>
    </div>
  </div>
  <e-layout>
    <e-layout-title slot="title" title="????????????" :moreLinkFlag="false"></e-layout-title>
    <e-layout-item
      slot="item"
      v-for="(item, index) in recommendVideo"
      :key="index"
      :item="item"
      :style="{ marginRight: `${(index + 1) % 5 === 0 ? '0px' : '20px' }` }"
    ></e-layout-item>
  </e-layout>
  <e-layout>
    <e-layout-title slot="title" title="????????????" :moreLinkFlag="false"></e-layout-title>
  </e-layout>
  <e-carousel :picLength="hotVideo.length">
    <e-carousel-item
      slot="item"
      v-for="(item, index) in hotVideo"
      :itemIndex="`${index + 1}`"
      :key="index"
      :item="item"
      :style="{ marginRight: '20px' }"
    ></e-carousel-item>
  </e-carousel>
  <e-footer></e-footer>
  <e-back-top></e-back-top>
</div>
</template>

<script>
import m3u8Parser from 'm3u8-parser'
import commonMixins from '@/mixins/common'
import ua from '../../utils/ua'

export default {
  mixins: [commonMixins],

  data () {
    return {
      uiType: 'player',
      pageType: 'player',
      videoId: '',
      videoDetailId: '',
      category: 0,
      sourcesList: [],
      vOptions: {
        sources: [],
        controls: true,
        width: 1150,
      },
      videoInfos: {
        intro: '',
      },
      videoSummaryShow: false,
      videoSummaryName: '??????',
      hotVideo: [],
      recommendVideo: [],
      hlsPlayList: [],
      videoExists: true // ????????????????????????
    }
  },
  watch: {
  },
  created() {
    this.videoId = this.getUrlParam('videoId')
    this.videoDetailId = this.getHashValue() ? this.getHashValue() : null
    this.fetchVideoData()
    this.fetchRecommendData()
    this.fetchHotVideoData()
  },
  methods: {
    handleUiTypeChange(type) {
      this.uiType = type
    },
    getHashValue() {
      let hash = window.location.hash
      if (hash) {
        return hash.slice(1)
      }
      return null
    },
    changeSummary(val) {
      this.videoSummaryShow = !val
      this.videoSummaryName = val ? '??????' : '??????'
    },
    changeVideoWidth(width) {
      this.vOptions.width = width
    },
    goToVideo(videoId, videoDetailId, idxInVideoList) {
      this.videoBaseId = videoId
      this.videoDetailId = videoDetailId
      this.videoInfos.currentIndex = idxInVideoList
      this.videoInfos.videoDetailId = videoDetailId
      window.location.href = '#' + videoDetailId

      this.vOptions.sources = this.getVideoSources()

      if (!ua.ie10) this.parseHlsList()

      this.postVideoData()
    },
    getVideoSources() {
      const videoBaseUrl = 'https://video.youxiang0210.com/'
      let sources = []

      function compareResolutions(a, b) {
        if (!a.res || !b.res) { return 0 }
        return (+(b.res).slice(0, -1)) - (+(a.res).slice(0, -1))
      }

      const resMap = {
        '240P': '??????',
        '480P': '??????',
        '720P': '??????',
        '1080P': '1080P',
      }
      let currentIndex = this.videoInfos.currentIndex
      // ?????????ie 10????????????mp4
      if (ua.ie10) {
        const mp4Sources = this.videoInfos.vVideoDetailRspVos[currentIndex].videoResourceStatusVo.mp4Claritys
        for (let i = 0, j = mp4Sources.length; i < j; i++) {
          sources.push({ res: mp4Sources[i]['clarity'], type: 'video/mp4', src: videoBaseUrl + mp4Sources[i]['url'], label: resMap[mp4Sources[i]['clarity']] })
        }
        sources = sources.sort(compareResolutions)
      } else {
        sources = [{
          src: videoBaseUrl + this.videoInfos.vVideoDetailRspVos[currentIndex].videoResourceStatusVo.m3u8Clarity.url,
          type: 'application/x-mpegURL',
        }]
      }
      return sources
    },
    // ?????????????????????(??????????????????)
    postVideoData() {
      this.$fetch('/web/videoBase/videoBaseInfo', {
        videoBaseId: this.videoId,
        videoDetailId: this.videoDetailId,
      })
    },
    async fetchVideoData() {
      let res = await this.$fetch('/web/videoBase/videoBaseInfo', {
        videoBaseId: this.videoId,
        videoDetailId: this.videoDetailId,
      })
      if (res.code === 30003) { // ???????????????
        this.videoExists = false
      }
      if (res.code === 0) {
        this.videoInfos = res.data
        this.category = this.videoInfos.categories
        if (this.category === 2) {
          this.changeVideoWidth(870)
        }
        this.vOptions.sources = this.getVideoSources()
        this.vOptions.controls = true
        this.videoInfos.intro = this.videoInfos.intro ? this.videoInfos.intro : ''
        if (!ua.ie10) this.parseHlsList()
      }
    },
    parseHlsList() {
      // FIXME ?????????
      const videoBaseUrl = 'https://video.youxiang0210.com/'
      let currentIndex = this.videoInfos.currentIndex
      fetch(videoBaseUrl + this.videoInfos.vVideoDetailRspVos[currentIndex].videoResourceStatusVo.m3u8Clarity.url, {
      }).then((res) => {
        return res.text()
      }).then((res) => {
        const parser = new m3u8Parser.Parser()

        parser.push(res)
        parser.end()

        var parsedManifest = parser.manifest
        if (parsedManifest.playlists) {
          this.hlsPlayList = parsedManifest.playlists
        }
      })
    },
    async fetchRecommendData() {
      let res = await this.$fetch('/web/channel/recommend', {
        videoBaseId: this.videoId,
        limit: 10,
      })
      if (res.code === 0) {
        this.recommendVideo = res.data
      }
    },
    async fetchHotVideoData() {
      let res = await this.$fetch('/web/search/hotVideo', {
        videoBaseId: this.videoId,
        pageSize: 10,
      })
      if (res.code === 0) {
        this.hotVideo = res.data
      }
    },
  },
}
</script>

<style lang="postcss" module>
.bg {
  width: 100%;
  background: #222222;
  height: 530px;
  margin-top: 58px;
}
.v-content {
  margin: 0 auto;
  margin-bottom: 40px;
  width: 1150px;
  max-width: 1150px;
}
.videoInfo {
  overflow: hidden;
  width: 1150px;
  margin: 0 auto;
  .videoTitle {
    font-size: 24px;
    color: #272727;
    margin-top: 14px;
    font-weight: normal;
  }
  .videoActor, .videoSummary {
    margin-top: 12px;
    label {
      float: left;
      color: #999;
    }
    p {
      color: #272727;
    }
  }
  .videoActor {
    p {
      overflow: hidden;
    }
  }
  .videoSummary {
    label {
      line-height: 23px;
    }
    p {
      line-height: 23px;
      overflow: hidden;
      text-overflow: ellipsis;
      color: #626262;
      span.videoSummaryMore {
        cursor: pointer;
        color: #FABE00;
      }
    }
  }
}
</style>



